.responsive-container {
  margin-top: 90px;
  width: 250px;
  margin-left: 600px; /* Default margin for large screens */

  /* Media query for small screens */
  @media (max-width: 600px) {
    margin-left: 10px; /* Adjust the margin for small screens */
  }

  /* Media query for medium screens */
  @media (min-width: 601px) and (max-width: 1200px) {
    margin-left: 100px; /* Adjust the margin for medium screens */
  }

  /* Media query for large screens */
  @media (min-width: 1201px) {
    margin-left: 330px; /* Default margin for large screens */
  }
}
